.separator{
    display:flex;
    flex-direction:row;
    align-items:center;
}
.separator__heading{
    @include heading;
}
.separator__dot{
    width:7px;
    height:7px;
    background:$hr-grey;
    border-radius:50%;
    margin-left:1.5em;
}
.separator__line{
    width:75%;
    margin-left:0;
    border-top:1px solid $hr-grey;
    border-bottom:none;
}
.separator__prev, .separator__next{
    display:none;
}
@media screen and (min-width:$viewport-11){
.separator__prev, .separator__next{
    background:$text-grey;
    border-radius:5px;
    border:none;
    height:16px;
    width:16px;
    margin-left:0.5em;
    text-align:center;
    cursor:pointer;
    transition:all 0.5s ease-in;
    &:hover{
        background:$base-orange;
    }
    &:focus{
        background:$base-orange;
    }
    &:active{
        background:$base-orange;
    }
}
.separator__prev{
    transform:rotate(180deg);
}
}
