.blog{
    padding:2em 0 4em 0;
    font-size:12px;
}
.blog__heading{
    @include blog-heading;
    padding-left:0.5em;
}

.blog__heading_strong{
    @include blog-heading;
    font-weight:700;
    cursor:pointer;
}
.blog__heading_italic{
    @include blog-heading;
    font-style:italic;
    color:$white;
    }
@media screen and (min-width:$viewport-5){
    .blog{
        padding:3.5em 0 4em 0;
    }
}
