.stages{
    padding:3em 0 0 0;
}
.stages__text{
    @include regular-text;
    padding:0 1em 2em 1em;
    color:$regular-text-grey;
}
.stages__diagram{
    display:none;
}

.stage{
    padding:0.5em;
}

@media screen and (min-width:$viewport-5){
    .stages__diagram{
        display:flex;
        flex-direction:row;
        justify-content:center;
    }
    .stages{
        order:2;
        width:60%;
        padding:3em 2em 4em 0;
    }
   
}
@media screen and (min-width:$viewport-11){
    .stages{
        order:0;
    }
}


.base {
    stroke-dasharray:131.9px;
    stroke-dashoffset:131.9px;
    stroke:$base-green;
    stroke-width:15px;
    fill:transparent;

}
.stage-1-done {
    stroke-dasharray:65.9px 197.8px;
    stroke-dashoffset:131.9px;
    stroke:$base-orange;
    stroke-width:15px;
    fill:transparent;

}

.stage-2-done {
    stroke-dasharray:85.9px 177.8px;
    stroke-dashoffset:131.9px;
    stroke:$base-orange;
    stroke-width:15px;
    fill:transparent;

}
.stage-3-done {
    stroke-dasharray:105.9px 157.8px;
    stroke-dashoffset:131.9px;
    stroke:$base-orange;
    stroke-width:15px;
    fill:transparent;

}
.stage-4-done {
    stroke-dasharray:131.9px 131.9px;
    stroke-dashoffset:131.9px;
    stroke:$base-orange;
    stroke-width:15px;
    fill:transparent;

}
text{
    font-weight:800;
    fill:$text-grey;
}
.stage__number{
    font-size:25px;
}
